.auth__button {
  padding: 0;
  border: none;
  width: 358px;
  height: 50px;
  border-radius: 2px;
  background: #fff;
  color: #000;
  text-align: center;
  font-size: 18px;
  margin: 186px 0 0 0;
  transition: opacity 0.3s linear;
}

.auth__button:hover {
  opacity: 0.6;
  cursor: pointer;
}

@media (max-width: 767px) {
  .auth__button {
    font-size: 16px;
    width: 260px;
    height: 46px;
    margin: 148px 0 0 0;
  }
}
